<template>
  <div class="propertyPreOrderPayDetail" :class="{themeL:bindThemeL}">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
          <div class="rightBtn" @click="callProperty" v-if="isDisplayRightName">{{rightName}}</div>
      </template>
    </navigation-bar>
    <div class="propertyPreOrderPayDetailContent">
    <div class="propertyPreOrderPayDetailCard">
      <div class="user_info">
        <van-image class="user_img" :src="houseImg" />
        <div class="user_word">
          <div class="user_home">{{orderDetail.roomName}}</div>
          <div class="user_name">房主：{{orderDetail.customerName}}</div>
        </div>
      </div>
      <div style="display: flex;position: relative;height: 20px;width: 100%;">
        <div class="circleLeft"></div>
        <div class="lineStyle"></div>
        <div class="circleRight"></div>
      </div>
      <div class="list">
        <div class="item" style="border:none;font-size: 14px;" :style="{fontSize:largeFontSize}">
          预缴周期
          <span class="item-note" style="color:black;font-size: 14px;" :style="{fontSize:largeFontSize}">
            <span>{{months}}</span>个月
          </span>
        </div>
        <div style="display: flex;position: relative;height: 10px;width: 100%;">
          <div class="lineStyle"></div>
        </div>
        <div class="item" style="padding-bottom:0px;border: none;">
          <div style="color: #9a9a9a;font-size: 14px;" :style="{fontSize:largeFontSize}">固定费用项</div>
        </div>

        <div class="item" style="padding-top:0px;border: none">
          <div
            v-for="(item,cycleindex) in cycleOptionList"
            :key="cycleindex"
            style="vertical-align: center;margin-top: 16px;height: 20px;display: flex;"
          >
            <div style="display: flex;">
              <div class="fs14" style="height: 20px;">{{item.payOptionName}}</div>
              <div class="saleTag" v-if="item.discount<100">{{item.discount/10}}折</div>
            </div>

            <div
              class="item-note"
              style="height: 20px;display: flex;justify-content: flex-end;flex-grow:1;"
            >
              <div
                class="fs12"
                v-if="item.discount<100"
                style="height: 20px;margin-right:4px;text-decoration:line-through;"
              >￥{{item.advanceMoney| keepTwoNum}}</div>
              <div
                class="fs14"
                v-if="orderDetail.useDiscount==1"
                style="height: 20px;color:black"
              >￥{{item.chargeMoney| keepTwoNum}}</div>
              <div
                class="fs14"
                v-if="orderDetail.useDiscount==0"
                style="height: 20px;color:black"
              >￥{{item.advanceMoney| keepTwoNum}}</div>
            </div>
          </div>
        </div>

        <div
          style="display: flex;position: relative;height: 10px;width: 100%;"
          v-if="noCycleOptionList.length>0"
        >
          <div class="lineStyle"></div>
        </div>

        <div class="item" style="border:none;" v-if="noCycleOptionList.length>0">
          <div style="color: #9a9a9a;font-size: 14px;" :style="{fontSize:largeFontSize}">非固定费用项</div>
        </div>

        <div class="item" style="padding-top:0px;border: none;" v-if="noCycleOptionList.length>0">
          <div class="pt16" v-for="(item,nocycleindex) in noCycleOptionList" :key="nocycleindex">
            <span class="optionTitle">{{item.payOptionName}}</span>
            <span class="item-note" style="color:black;">￥{{item.chargeMoney| keepTwoNum}}</span>
          </div>
        </div>

        <div style="display: flex;position: relative;height: 20px;width: 100%;">
          <div class="circleLeft"></div>
          <div class="lineStyle"></div>
          <div class="circleRight"></div>
        </div>

        <div class="navContent">
          <div class="nav">
            <div class="orderDiv">
              <div class="orderDetail">
                <div style="font-size:12px;" :class="{orderDivSonThemeL:bindThemeL}">
                  <span class="orderDivSon">订单编号</span>
                  <input
                    type="text"
                    name="selectCopy"
                    id="selectCopy"
                    class="orderNumber"
                    v-model="billNo"
                    style="position: absolute;left:-100%;bottom:-5%;"
                    readonly
                  />
                  {{billNo}}
                </div>
                <button class="orderCopy" @click="copy()">复制</button>
              </div>
              <div class="orderDivSon">
                订单类型
                <span class="orderSpan">{{orderDetail.billType}}</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.payWay==1">
                支付方式
                <span class="orderSpan">微信</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.payWay==2">
                支付方式
                <span class="orderSpan">支付宝</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.billStatus==1">
                支付时间
                <span class="orderSpan">{{orderDetail.payTime}}</span>
              </div>
              <div class="orderDivSon">
                创建时间
                <span class="orderSpan">{{orderDetail.createdTime}}</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.getPoints">
                可获积分
                <span class="orderSpan">+{{orderDetail.getPoints}}</span>
              </div>
            </div>
          </div>
          <van-divider dashed="true" />
          <div class="total_and_deduction">
            <div class="total_money">
              <div class="total_name">订单总额</div>
              <div class="total_num">￥{{totalAmount| keepTwoNum}}</div>
            </div>
            <div class="deduction_point">
              <div class="deduction_name">积分抵扣</div>
              <div class="deduction_num">-￥{{deductionMoney| keepTwoNum}}</div>
            </div>
          </div>
          <van-divider dashed="true" />
          <div class="payBottom">
            <div class="payBottomStatueBlue" v-if="orderDetail.billStatus==1">已支付</div>
            <div class="payBottomStatueRed" v-if="orderDetail.billStatus==0">待支付</div>
            <div class="payBottomStatueBlack" v-if="orderDetail.billStatus==2">已取消</div>
            <div class="payBottomText">
              <span class="payBottomSpan">实付：</span>
              <span class="payBottomMoney">￥{{orderDetail.unionPayMoney| keepTwoNum}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    </div>

    <div style="height:87px" v-if="orderDetail.billStatus==0"></div>
    <div class="order_pay" v-if="orderDetail.billStatus==0">
      <div class="order_num" @click="cancelOrder()">取消订单</div>
      <div class="soon_pay" @click="goToPay()">去支付</div>
    </div>
    <!-- <property-order-goto-pay :amount="unionPayMoney" @goToPay="goToPay"></property-order-goto-pay> -->
    <property-pay-popup
      v-if="payWindowShow"
      :payParam="payParam"
      :isAlipay="isAlipay"
      :isWechart="isWechart"
      @closePopup="closePopup"
      @checkOrderStatus="checkOrderStatus"
    ></property-pay-popup>
  </div>
</template>

<script>
import {
  getPrepayOrderDetailService,
  prepareToPayPreOrderService,
  preOrderCancelService
} from "../../../services/propertyBillService";
import propertyOrderGotoPay from "./propertyOrderGotoPay";
import propertyPayPopup from "./propertyPayPopup";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Vue from "vue";
import { Dialog } from "vant";
Vue.use(Dialog);
export default {
  name: "propertyPreOrderPayDetailDetail",
  data() {
    return {
      billNo: "",
      orderDetail: {}, //订单详情
      isAllPoints: 0, //使用全部积分标识
      pointAviable: 1000, //可用积分
      totalAmount: 0.0,
      unionPayMoney: 0.0,
      discountMoney: 0.0,
      deductionMoney: 0,
      deductionPoints: 0,
      months: 0,
      cycleOptionList: [],
      noCycleOptionList: [],
      payWindowShow: false, //支付弹窗
      isAlipay: false,
      isWechart: false,
      numShow: false, //联系物业是否显示
      headerItemWidth:{
          width:'110px'
      },
      title:'订单支付',
      rightName:'联系物业',
      isDisplayRightName:false,
      switchOnImg: require("../../../assets/images/home/property/payment/switch_on.png"),
      switchOffImg: require("../../../assets/images/home/property/payment/switch_off.png"),
      addImg: require("../../../assets/images/home/property/advancePayment/bg_add.png"),
      cutImg: require("../../../assets/images/home/property/advancePayment/bg_cut.png"),
      houseImg: require("../../../assets/images/home/property/payment/house_house.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      largeFontSize: this.$curTheme==0?"14px":"17px"
    };
  },
  components: {
    NavigationBar,
    "property-order-goto-pay": propertyOrderGotoPay,
    "property-pay-popup": propertyPayPopup
  },
  mounted() {
    this.billNo = this.$route.params.billNo;
    this.getPreOrderDetail();
  },
  methods: {
    getPreOrderDetail() {
      let reqData = { billNo: this.$route.params.billNo };
      this.cycleOptionList = [];
      this.noCycleOptionList = [];
      getPrepayOrderDetailService(reqData)
        .then(result => {
          if (result.resultCode == 200) {
            this.orderDetail = result.data;
            //add for ZHSQYYPT-8358
            this.isDisplayRightName = this.orderDetail.propertyPhoneNumber != '物业电话尚未配置';

            //获得每个收费项目
            this.updateOptionList(result.data.detailList);

            this.totalAmount = parseFloat(result.data.payMoneyTotal).toFixed(2);
            this.unionPayMoney = parseFloat(result.data.unionPayMoney).toFixed(
              2
            );
            this.months = result.data.months;
            if (
              result.data.deductionPoints != null &&
              result.data.deductionPoints != ""
            ) {
              this.deductionMoney = parseFloat(
                result.data.deductionMoney
              ).toFixed(2);
              this.deductionPoints = result.data.deductionPoints;
            } else {
              this.deductionPoints = 0;
            }

            // 获取用户积分
            // this.getUseOrderLimitFn();
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    updateOptionList(detailList) {
      for (var i = 0; i < detailList.length; i++) {
        if (detailList[i].feeType == 0) {
          this.cycleOptionList.push(detailList[i]);
        } else {
          this.noCycleOptionList.push(detailList[i]);
        }

        //计算物业折扣优惠金额
        var dismoney = this.discountMoney;
        var listmoney = detailList[i].discountMoney;
        this.discountMoney = dismoney + listmoney;
      }
    },
    cancelOrder() {
      Dialog.confirm({
        title: "提示",
        message: "确认要取消订单吗？"
      })
        .then(() => {
          console.log("取消确认回调");
          var reqData = {
            billNo: this.billNo,
            cancelWay: "01"
          };
          preOrderCancelService(reqData)
            .then(result => {
              //请求成功
              if (result.resultCode == 200) {
                this.$toast({
                  duration: 500,
                  message: "取消成功",
                  onClose: () => {
                    this.getPreOrderDetail();
                  }
                });
              } else {
                this.$toast(result.msg ? result.msg : "取消失败，请稍后重试！");
              }
            })
            .catch(error => {
              //加载失败后，关闭加载等待的文字
              this.$toast("网络异常，请稍后重试！");
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    onClickLeft() {
      this.$router.back();
    },
    /* 立即支付 */
    goToPay() {
      let reqData = {
        billNo: this.billNo,
        deductionPoints: this.deductionPoints,
        deductionMoney: this.deductionMoney,
        unionPayMoney: this.unionPayMoney
      };
      prepareToPayPreOrderService(reqData)
        .then(result => {
          if (result.resultCode == 200) {
            /* 获取支付参数 */
            this.payParam = result.data;
            /* 判断支持的支付方式 */
            if (result.data.IS_ALI_PAY == "1") {
              this.isAlipay = true;
            }
            if (result.data.IS_WX_PAY == "1") {
              this.isWechart = true;
            }
            /* 支付弹窗 */
            this.payWindowShow = true;
            // /*监听ios支付结果 检测当前订单状态*/
            window.broadcaster.addEventListener("paymentBack", this.payCallBack);
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    payCallBack() {
      this.checkOrderStatus();
    },
    checkOrderStatus() {
      /* 取消监听，避免重复监听 */
      window.broadcaster.removeEventListener("paymentBack",this.payCallBack);
      /* 获取订单数据 */
      let reqData = { billNo: this.billNo };
      getPrepayOrderDetailService(reqData)
        .then(result => {
          if (result.resultCode == 200) {
            if (result.data.billStatus == 1) {
              //订单状态为已支付，跳转支付成功界面
              this.$router.push({
                name: "paymentPaySuccess",
                params: { orderData: result.data }
              });
            } else {
              //支付失败，返回列表页
              this.$toast({
                duration: 500,
                message: "支付失败",
                onClose: () => {
                  this.$router.back();
                }
              });
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    closePopup() {
      this.payWindowShow = false;
    },
    /**
     * 复制订单号
     */
    copy() {
      cordova.plugins.clipboard.copy(
        this.billNo,
        () => {
          this.$toast("复制成功");
        },
        function() {
          console.log("error");
        }
      );
    },
    callProperty() {
      if (device.platform == "Android") {
        Dialog.confirm({
          title: "拨打电话",
          message: this.orderDetail.propertyPhoneNumber
        }).then(() => {
          window.location.href = "tel:" + this.orderDetail.propertyPhoneNumber;
        });
      } else {
        window.location.href = "tel:" + this.orderDetail.propertyPhoneNumber;
      }
    }
  }
};
</script>

